body {
  margin: 0px;
  padding: 0px; }

.title {
  text-align: center; }

.board-frame-tw {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 20px;
  position: relative;
  margin: 0 auto 0 auto;
  width: 410px;
  height: 245px;
  background-color: #0000C6;
  color: #fff;
  margin-top: 20px; }
  .board-frame-tw .first-row {
