    width: 410px;
    font-family: "Microsoft JhengHei"; }
    .board-frame-tw .board-row .board-subtitle {
      position: relative;
      width: 50px;
      font-weight: bold; }
      .board-frame-tw .board-row .board-subtitle span:nth-child(1) {
