    .board-frame-tw .board-row .r2-unit {
      padding: 29px 10px 0px 10px;
      font-weight: bold; }

.control {
  width: 350px;
  margin: 50px auto 0 auto;
  font-family: "Microsoft JhengHei";
  color: #007500; }
  .control fieldset {
    border: 2px solid #007500; }
  .control form {
    line-height: 30px; }
    .control form input {
      width: 50px; }
    .control form .full-width-input {
      width: 80px; }
    .control form .memo-input textarea {
      width: 320px;
      height: 50px;
      resize: none; }
    .control form span {
      font-size: 8px;
      color: gray;
      vertical-align: bottom;
      padding-left: 3px; }



